html { height: 100%; }
body { max-height: 100%; }
.App {
  text-align: center;
}
:root {
  --SIZE: 3;
  --TEMPS: 100px;
  --visibilityStatus: "none";
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.PLayer {
  display: grid;
  grid-template-columns: auto auto auto auto;
}
.grid {
  display: grid;
  grid-template-rows: repeat(var(--SIZE), calc(var(--TEMPS) * 0.5625));
  grid-template-columns: repeat(var(--SIZE), var(--TEMPS));
}
.item {
  background: grey;
  display: flex;
  height: auto;
  justify-content: center;
  text-align: center;
}
.item:before {
  content: "";
  height: 0;
  width: 0;
}

.dropdown {
  display: none;
  padding-top: 50px;
  padding-bottom: 50px;
}

.trigger{
  position: absolute;
  top: 100px;
  width: 100%;
  height: 50%;
}

.trigger:hover + .dropdown{
  display: block;
}

.dropdown:hover{
  display: block;
}

.tempGrid {
  padding-left: 3em;
  padding-right: 3em;
  margin-top: 3em;
}
